.reminder-wrap {
	position: relative;
	display: block;
	border-radius: var(--border-radius);
	padding: 8rem 1rem;
	overflow: hidden;
	margin-top: 3rem;
	box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
	@include max-md {
		padding: 3rem 0.75rem;
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 320px;
		transform: translateX(-20%);
		@include max-md {
			width: 240px;
		}
		@include max-xs {
			width: 220px;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 320px;
		transform: translateX(20%);
		@include max-md {
			width: 240px;
		}
		@include max-xs {
			width: 220px;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-35%, 60%);
		z-index: 2;
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(35%, 60%) scaleX(-1);
		z-index: 2;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
